.slides {
  background-color: $maincolor;
  overflow: hidden;
  width: 100%;
  position: relative;

  .main-title, .main-subtitle {
    position: absolute;
    z-index: 2;
    font-family: "Yeseva One", sans-serif;
    color: $white;
    text-align: right; }

  .main-title {
    right: 387px;
    bottom: 268px;
    &__title {
      @include resetmargin;
      margin-bottom: 6px;
      font-size: 9.6rem;
      font-weight: normal; } }

  .main-subtitle__promotion-link {
    text-decoration: none;
    color: $white;
    right: 387px;
    opacity: 0;
    pointer-events: none;
    transition: opacity $transition/2;
    &--activate {
      opacity: 1;
      pointer-events: all;
      transition: opacity $transition 700ms; } }

  .main-subtitle {
    position: relative;
    right: 0;
    bottom: 268px;
    @include resetmargin;
    font-weight: normal;
    font-size: 3.6rem;
    line-height: 52px; }

  .main-cursive {
    height: 691px;
    width: 150px;
    position: absolute;
    z-index: 2;
    left: 63px;
    bottom: 0;
    @include media("<large") {
      display: none; }
    &__slide {
      width: 205px; }
    &__slide-text {
      top: -30px;
      left: -250%;
      width: 1022px;
      @include media("<huge") {
        top: 50px;
        font-size: 11rem;
        left: -280%; } } } }

.master-slider {
  .swiper-pagination-bullets {
    width: auto;
    right: 373px;
    bottom: 65px;
    left: auto;
    .swiper-pagination-bullet {
      background-color: transparent;
      opacity: 1;
      margin: 0 6px;
      position: relative;
      height: 26px;
      width: 26px;
      &:focus {
        outline: none; } }
    .swiper-inside-bullet {
      position: absolute;
      display: block;
      width: 8px;
      height: 8px;
      background-color: $white;
      border-radius: 50%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      mix-blend-mode: difference; } }

  .swiper-lazy-preloader {
    position: absolute; }
  .swiper-slide {
    filter: brightness(75%);
    position: relative;
    transform: scale(1);
    transition: transform 5000ms; }
  .swiper-container {
    width:  100%;
    height: calc(100vh - 84px); }
  .slider-pic {
    img {
      object-fit: cover;
      width: 100%;
      height: calc(100vh - 84px); } }
  .swiper-wrapper {
    position: relative; }
  .slide-zoom {
    transform: scale(1.1); } }





#block-search-main {
  position: absolute;
  right: 84px;
  bottom: 76px;
  background: #fff;
  border-radius: 5px;
  width: 100%;
  max-width: 245px;
  z-index: 5; }

